import React from 'react';
import Link from 'gatsby-link';

export default({ data }) =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-12">
                <h1 className="text-center">The Book of Arda</h1>
                <p>The lands of Arda are constantly in flux, as the powerful magic of dark and light play out on the land as if it were a stage. Below are the stories that recount the most tumultuous encounters between Good and Evil and their effects on the landscape.</p>
                {data.allMarkdownRemark.edges.map(({ node }) =>
                    <div key={node.id} className="col-lg-12" style={{display: 'block'}}>
                        <h4><Link to={node.frontmatter.path}>{node.frontmatter.title}</Link></h4>
                    </div>
                )}
            </div>
        </div>
    </div>
</div>

export const query = graphql `
query BookPosts {
    allMarkdownRemark(
        sort: {fields: [frontmatter___title]},
        filter: {fileAbsolutePath: {regex: "/(books)/.*\\.md$/"}}
        ) {
        edges {
            node {
                id
                frontmatter {
                    title
                    path
                }
            }
        }
    }
}
`


